<template>
  <div class="upload">
    <input type="file" ref="gg" v-show="false" @change="changeImage" />
    <div class="area" @dragover.prevent="dragoverfn" @drop.prevent="dropfn">
      <div v-show="show">
        <div class="arrow" ref="arrow">
          <img src="../../img/arrow.png" style="width: 50px; height: 50px" />
        </div>
        <h4 class="uploading_drag">
          You can drag and drop .jpg here to add them
        </h4>
      </div>
      <div v-show="!show">
        <img :src="image" alt="" style="width: 200px; height: 200px" />
      </div>
    </div>
    <textarea
      v-model="caption"
      placeholder="Caption..."
      maxlength="120"
    ></textarea>
    <a
      href="#"
      class="u_sub"
      ref="sub"
      name="sub"
      @mousedown="mdown"
      @mouseup="mup"
      @click="imagepost"
      >Submit</a
    >
    <a
      href="#"
      class="u_can"
      ref="can"
      name="can"
      @mousedown="mdown"
      @mouseup="mup"
      @click="del"
      >Cancel</a
    >
  </div>
</template>

<script>
import { checkfile } from "@/assets/methods/checkext.js";
import { upload } from "@/network/api.js";

export default {
  name: "upload",
  data() {
    return {
      show: true,
      validate: true,
      caption: "",
      image: "",
    };
  },
  methods: {
    changeImage(e) {
      let arr = [...e.target.files]
      if(arr.length < 1) {
        console.log('error')
        return
      }
      let files = arr[0];
      this.validate = checkfile(files);
      this.show = !this.validate;
      if (this.validate == true) {
        this.imageloader(files);
      }
    },
    dragoverfn() {
      this.show = false;
    },
    dropfn(e) {
      let arr = [...e.dataTransfer.files]
      if(arr.length < 1) {
        console.log('error')
      }
      let files = arr[0];
      this.validate = checkfile(files);
      this.show = !this.validate;
      if (this.validate == true) {
        this.imageloader(files);
      }
    },
    mdown(e) {
      const item = this.$refs[e.target.name];
      item.style.boxShadow = "1px 2px #444444 inset";
    },
    mup(e) {
      const item = this.$refs[e.target.name];
      item.style.boxShadow = "1px 2px 3px gray";
    },
    imageloader(file) {
      const image = new Image();
      const reader = new FileReader();
      reader.onload = (e) => {
        this.image = e.target.result;
      };
      reader.readAsDataURL(file);
      this.file = file;
    },
    async imagepost() {
      if (this.file instanceof File) {
        const formData = new FormData();
        formData.append("caption", this.caption);
        formData.append("picture", this.file);
        try {
          let result = await upload(formData, this.$store.state.token);
          console.log(result);
          this.del()
        } catch (error) {
          console.log(error);
        }
      }else {

      }
    },
    del() {
      this.file = null
      this.image = ""
      this.caption = ""
      this.show = true
    }
  },
};
</script>
<style scoped>
.upload {
  height: 430px;
  position: relative;
}
.area {
  position: relative;
  left: 50%;
  top: 30px;
  transform: translate(-50%);
  height: 200px;
  width: 200px;
  border: 3px dashed darkgray;
}

a {
  position: relative;
  text-decoration: none;
  display: block;
  text-align: center;
  width: 200px;
  left: 50%;
  transform: translate(-50%);
  top: 52px;
  height: 30px;
  box-sizing: border-box;
  /* background-color: #0036AE; */
  border-radius: 5px;
  margin-top: 8px;
  padding: 5px;
  box-shadow: 1px 2px 3px gray;
}

.u_sub {
  background-color: #0036ae;
  color: whitesmoke;
}

.u_can {
  background-color: #e9ecef;
  color: #000000;
}

.arrow {
  position: absolute;
  left: 50%;
  top: 40px;
  transform: translate(-50%);
  animation: updown 2.5s infinite ease-in-out;
}

@keyframes updown {
  0% {
    top: 15%;
  }
  50% {
    top: 30%;
  }
  100% {
    top: 15%;
  }
}

h4 {
  position: absolute;
  top: 110px;
  text-align: center;
}

textarea {
  position: relative;
  left: 50%;
  transform: translate(-50%);
  top: 50px;
  width: 200px;
  height: 70px;
  resize: none;
}
</style>